@import url(https://fonts.googleapis.com/css?family=Sigmar+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.fa-star {
  margin: 0.5em;
  font-size: 40px;
  color: rgb(255, 229, 81);

}
/* .stars{
  display:inline-block;
} */

.iconRow{
  display: inline-block;
}

span {
  display: inline-block;
  margin: 1em;
  text-align: center;
  background-color: rgb(134, 223, 82);
  color: rgb(0, 78, 0);
  width: 2em;
  font-size: 1.5em;
  border-radius: 50%;
  cursor: pointer;  
}

.numbers {
  border-style: solid;
  margin: 0.5em;
}

.selected {
  background-color: #eee;
  color: #ddd;
	cursor: not-allowed;  
}

.used {
  background-color: #eee;
  color: #ddd;
	cursor: not-allowed;  
}

h1{
  font-size: 2.5em;
  font-family: 'Sigmar One', cursive;
  color:white;
  margin-top: 0;
}

.header{
  padding-top: 1%;
  background: #ffb76b; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #ffb76b 7%,#ffa73d 35%,#ff7c00 82%,#ff7c00 82%,#ff7f04 100%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(315deg, #ffb76b 7%,#ffa73d 35%,#ff7c00 82%,#ff7c00 82%,#ff7f04 100%);
  background: linear-gradient(135deg, #ffb76b 7%,#ffa73d 35%,#ff7c00 82%,#ff7c00 82%,#ff7f04 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffb76b', endColorstr='#ff7f04',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.btn{
  width: 3em;
  font-size: 1em;
  margin-right: 2em;
}

.btn-verify{
  width: 3em;
  margin-right: 2em;
  font-size: 1em;
}

.btn-redo{
  width: 4em;
  font-size: 1em;
}

.done{
  font-size: 2em;
  font-family: 'Sigmar One', cursive;
}

.btn-play-again{
  width: 6em;
  font-size: 0.5em;
}

.faRedo {
  margin-right: 0.5em;
}
